import React, { useContext, useState } from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { db } from '../db/db';
import { MainContext } from '../context/MainContext';
import { CardMedia, Grid } from '@mui/material';
import prefImage1 from '../icons/pref1.png';
import prefImage2 from '../icons/pref2.png';
import { useNavigate } from 'react-router-dom';


export default function LoginForm({ }) {
    const { fetchUser } = useContext(MainContext);
    const [status, setStatus] = useState('');
    const [id, setId] = useState('');
    const navigate = useNavigate();

    const handleInputChange = (event) => {
        const input = event.target.value.toUpperCase(); // Convertir en majuscules
        if (input.length <= 5) { // Limiter  5 caractres
            setId(input);
        }
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
        await requestUser(id);
        navigate('/parametre')
    };

    async function requestUser(id) {
        try {
            await db.user.clear();  // Effacer la base de donnes des utilisateurs
            setStatus('Base de donnes des utilisateurs vide');
            const response = await fetch(`https://www.langazobs.langazel.asso.fr/api/v1/user?resourceid=${id}`, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            });

            if (!response.ok) {
                throw new Error('Erreur rseau ou serveur');
            }

            const data = await response.json();

            // Enregistrer les donnes de l'utilisateur dans la base de donnes IndexedDB
            await db.user.add({
                nom: data.nom,
                prenom: data.prenom,
                idmembre: data.idmembre
            });

            setStatus('Donnes de l\'utilisateur API charges avec succs');
            fetchUser();
        } catch (error) {
            setStatus(`Erreur lors de la mise  jour des donnes de l'API : ${error.message}`);
        }
    }

    async function handleAddFictitiousUser() {
        const fictitiousUser = {
            email: 'fictitious@user.com',
            password: 'hashed_password',
            firstName: 'John',
            lastName: 'Doe'
        };
        try {
            const id = await db.user.add(fictitiousUser);
            setStatus(`Utilisateur fictif ajout avec l'ID ${id}`);
            fetchUser();
        } catch (error) {
            setStatus(`chec de l'ajout de l'utilisateur fictif: ${error}`);
        }
    }

    async function handleClearDatabase() {
        try {
            await db.user.clear();
            setStatus('Base de donnes des utilisateurs vide');
            fetchUser();
        } catch (error) {
            setStatus(`chec du vidage de la base de donnes: ${error}`);
        }
    }

    return (
        <Grid container>
            <Grid item xs={12}>
                <Card sx={{ marginBottom: "10px" }}>
                    <Typography variant="h6">
                        Pour vous connecter, rcuprer votre ID d'utilisateur<br />
                        Sur le site, en haut  droite, cliquez sur votre nom<br />
                        Choisisez "Vos Prfrences"<br />
                        Votre identifiant unique se trouve en haut  gauche<br />
                        <img src={prefImage1} alt="preferences" />
                        <img src={prefImage2} alt="preferences" />
                    </Typography>
                </Card>
            </Grid>
            <Grid item>
                <Card sx={{ height: "450px", width: "100%", marginBottom: "20px" }}>
                    <form onSubmit={handleSubmit}>
                        <CardContent>
                            <TextField
                                label="Identifiant"
                                name="Identifiant"
                                value={id}
                                onChange={handleInputChange}
                                inputProps={{ maxLength: 5 }} // Limiter  5 caractres
                                style={{ textTransform: 'uppercase' }} // Afficher en majuscules

                            />
                        </CardContent>
                        <CardActions>
                            <Button type="submit" size="small">Valider</Button>
                            <Button onClick={handleAddFictitiousUser} size="small">Ajouter un utilisateur fictif</Button>
                            <Button onClick={handleClearDatabase} size="small">Vider la base de donnes</Button>
                        </CardActions>
                    </form>
                    {status && (
                        <Typography variant="body1" color="error" sx={{ marginTop: "20px" }}>
                            {status}
                        </Typography>
                    )}
                </Card>
            </Grid>
        </Grid>
    );
}
