import React, { useContext, useState } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Button from '@mui/material/Button';
import SyncIcon from '@mui/icons-material/Sync';
import Typography from '@mui/material/Typography';
import { MainContext } from '../context/MainContext';
import { db } from '../db/db';

export default function Synchro() {
    const { obsCount, setObsCount } = useContext(MainContext);
    const [syncing, setSyncing] = useState(false);
    const [syncError, setSyncError] = useState(null);
    const [syncSuccess, setSyncSuccess] = useState(false);

    const syncData = async () => {
        setSyncing(true);
        setSyncError(null);
        setSyncSuccess(false);

        try {
            // Rcuprer les donnes de toutes les tables
            const fiches = await db.fiche.toArray();
            const obs = await db.obs.toArray();
            const lignes = await db.ligne.toArray();
            const users = await db.user.toArray();

            // Crer un objet pour contenir toutes les donnes
            const dataToSync = {
                fiches,
                obs,
                lignes,
                users
            };

            console.log('Data to sync:', dataToSync);

            // Envoyer les donnes  l'API
            const response = await fetch('https://www.langazobs.langazel.asso.fr/api/v1/sync', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(dataToSync)
            });

            if (!response.ok) {
                throw new Error('Erreur lors de la synchronisation des donnes');
            }

            const result = await response.json();
            console.log('Donnes synchronises avec succs:', result);

            // Si la synchronisation est russie, effacer les donnes des tables
            await db.fiche.clear();
            await db.obs.clear();
            await db.ligne.clear();
            setSyncSuccess(true);
            setObsCount(0);
        } catch (error) {
            console.error('Erreur lors de la synchronisation des donnes:', error);
            setSyncError(error.message);
        } finally {
            setSyncing(false);
        }
    };

    return (
        <div style={{ display: 'flex', justifyContent: 'center', marginTop: 20, height: '100vh' }}>
            <Card sx={{ width: 300, height: 300, display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
                <CardContent sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', height: '100%' }}>
                    <SyncIcon style={{ fontSize: 60, color: '#0e86a1' }} />
                    <Typography variant="h5" component="div">
                        Synchronisation
                    </Typography>
                    <Typography variant="body2" color="textSecondary">
                        Nombre d'observations  synchroniser : {obsCount}
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button onClick={syncData} variant="contained" color="primary" disabled={syncing} fullWidth>
                        {syncing ? 'Synchronisation en cours...' : 'Synchroniser les donnes'}
                    </Button>
                </CardActions>
                {syncSuccess && <Typography variant="body2" color="green">Synchronisation russie!</Typography>}
                {syncError && <Typography variant="body2" color="red">Erreur : {syncError}</Typography>}
            </Card>
        </div>
    );
}
